.skills {
    background: #fcfafa;
    min-height: 100%;
    background-size: cover;
}


h1.heading {
    color: #0d1f2d;
    font-size: 60px;
    text-align: center;
    font-family: Poppins, sans-serif;
    font-weight: 700;
    margin-bottom: 10px;
    padding-bottom: 10px;
}

p.heading.p__color {
    margin-bottom: 0;
    text-align: center;
    line-height: 1.2;
}

.skills__box {
    border-radius: 10px;
    width: 600px;
    background-color: #0d1f2d;
    padding: 100px;
    transition: .4s ease;
    padding: 150px;
}

.skills__box:hover {
    transform: translateY(10px);
    background-image: linear-gradient(90deg,#0d1f2d,#798086); 
}

h1.skills__text {
    text-align: center;
    color: #ffffff;
    font-size: 40px;
    font-family: sans-serif;  
}

.icon {
    font-size: 54px;
    font-weight: 400;
    margin: 20px;
    display: inline-flex;
    color: #f9004d;
    transition: transform 0.4s ease; 
}

.icon:hover {
    transform: translate(10px, 10px);
}