.home__bg {
    min-height: 100%;
    background-color: #0d1f2d;
    background-size: cover;
    background-position: 50%;
    background-repeat: no-repeat;
    height: 100vh;
}

li.nav__items {
    font-size: 18px;
    padding-bottom: 15px;
}

.home__content {
    padding: 125px 0px;
}

h1.home__text {
    text-align: center;
    font-size: 18px;
    font-weight: 400;
    letter-spacing: 3px;
    color: rgba(198, 201, 216, .75)!important;
}

h2.home__text {
    text-align: center;
    font-size: 54px;
    line-height: 54px;
    font-weight: 800;
    font-family: Montserrat, sans-serif;
}

h3.home__text {
    text-align: center;
    font-size: 50px;
    font-weight: 800;
    font-family: Montserrat, sans-serif;
    line-height: 40px;
}

.header {
    transition: .4s all ease;
}

.header.active {
    position: fixed;
    z-index: 1000;
    width: 100%;
    padding: 0px 30px;
    background-color: #798086;
}