.workExperience {
    background: #0d1f2d;
}

h1.workExperience__heading {
    color: #fcfafa;
    font-size: 60px;
    text-align: center;
    font-family: Poppins,sans-serif;
    font-weight: 700;
    margin-bottom: 10px;
}

.workExperience__box {
    border-radius: 10px;
    width: 600px;
    background-color: #798086;
    padding: 50px;
    transition: .4s ease;
}

.workExperience__box:hover {
    transform: translateY(10px);
    background-image: linear-gradient(90deg,#798086,#fcfafa); 
}

.skills__box:hover p.skills__text.p__color{
    color: #fff!important;
}

.workExperience__text {
    color: #0d1f2d;
    font-size: 40px;
    padding: 10px;
    text-align: center;
    display: flex;
    gap: 175px;
}

h1.workExperience__text__1 {
    text-align: left;
    font-size: 18px;
    font-weight: 400;
    letter-spacing: 3px;
    color: #0d1f2d;
}

.pad {
    padding-bottom: 15px;
}