.about {
    background: #798086;
    min-height: 100%;
    background-size: cover;
}

img.about__img {
    border-radius: 10px;
    width: 70%;
}

h1.about__heading {
    color: #0d1f2d;
    margin-bottom: 10px;
    font-weight: 700;
    text-align: start;
    font-family: sans-serif;
    font-size: 60px;
}

button.about.btn.pointer {
    text-transform: capitalize;
    width: 195px;
    height: 55px;
    font-size: 18px;
    transition: .4s ease;
}

.about__button {
    padding: 20px 0px;

}

.up__to__top__btn {
    width: 40px;
    height: 40px;
    position: fixed;
    bottom: 3%;
    right: 2%;
    background-color: #44449B;
    border-radius: 50%;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: .4s all ease;
    z-index: 1000;
}