.contact {
    background-color: #fcfafa;
}

h1.hire__text {
    color: #0d1f2d;
    font-size: 60px;
    text-align: center;
}

p.hire__text {
    text-align: center;
}

.container {
    display: flex;
    justify-content: center;
    align-items: center;
}

input {
    display: block;
    width: 100%;
    padding: 0 20px;
    border: 2px solid rgba(0,0,0,.1);
    border-radius: 5px;
    -webkit-transition: all .3s ease;
    transition: all .3s ease;
    height: 80px;
    line-height: 60px;
    margin: 20px 0px;
    outline: none;
    color: #0d1f2d;
    font-size: 20px;
    letter-spacing: .1px;
    background: none;
    border: 2px solid #0d1f2d;
}